<template>
	<view class="bg">
		<view class="mask" v-if="loading" style="background-color:#F5F5F5;margin-top:0!important;" :style="{'height':loadHeight+'px'}">
			<view class="loading13" style="position:absolute;margin-top:-50upx;">
				<view></view>
				<view></view>
				<view></view>
				<view></view>
				<view></view>
			</view>
		</view>
		<!-- <view class="content-head" :style="{ top: top + 'px' }" @click="skipUrl('/pages/member/member', 3)">
			<image src="@/static/imgs/back.png" mode="aspectFill" style="width:20px;height:20px;margin:5px 0 0 -3px;"></image>
		</view> -->
		<view class="poster-bg">
			<image class="poster" :style="{ height: bgHeight + 'px' }" :src="bg" mode="aspectFill"></image>
			<view class="explain" :style="{ top: top + 'px' }" @click="closeTig">规则</view>
			<view class="poster-save p-left" :style="{'bottom':btnBottom+'px'}" @click="saveShareImg">保存海报</view>
			<view class="poster-save p-right" :style="{'bottom':btnBottom+'px'}" @click="shareLog">分享记录</view>
		</view>
		<view class="explain-tig" v-if="close">
			<view class="explain-content">
				<view class="explain-title">
					规则说明
					<image src="@/static/imgs/delx.png" class="close" @click="closeTig"></image>
				</view>
				<view class="explain-center">
					<!-- <rich-text :nodes="rule"></rich-text> -->
					<jyf-parser :html="rule" ref="article"></jyf-parser>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import api from "@/common/api.js";
var _self;
var wv;//计划创建的webview
export default {
	data() {
		return {
			top: 27,
			explain: 47,
			bgHeight: 695,
			close: false,
			userId: 0,
			bg: '',
			rule: '规则说明',
			pName: '',
			btnBottom: 30,
			isApp: true,
			
			one: false,
			loading: true,
			loadHeight: 600,
		}
	},
	onLoad() {
		_self = this;
		//uni.showLoading({ title:'加载中...',mask:true });
		var mobile = uni.getSystemInfoSync();
		_self.top = mobile.safeArea.top+7;
		_self.bgHeight = mobile.windowHeight;
		_self.loadHeight = _self.bgHeight;
		//console.log(mobile)
		// #ifdef H5
		_self.btnBottom = 15;
		// #endif
		//_self.explain = _self.top+40;
		// 是否登录
		var info = api.getLogins();
		if(info == false) {
			_self.skipUrl();
		}else{
			_self.userId = info.userId;
			_self.getShare();
		}
	},
	onHide() {
		_self.one = true;
	},
	onShow() {
		/* if(_self.one){
			var info = api.getLogins();
			if(info == false) {
				_self.skipUrl();
			}else{
				_self.userId = info.userId;
				_self.getShare();
			}
		} */
	},
	methods: {
		// 获取宣传规则信息
		getShare() {
			uni.request({
				url: api.apiData.myShare+'/userId/'+_self.userId,
				method: 'GET',
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e)=> {
					var res = e.data.Data;
					//console.log(res)
					_self.pName = res.posterName;
					// 设置背景图片
					api.imgCache(res.poster, (img)=>{
						_self.bg = img;
					});
					//_self.bg = res.poster;
					// 规则说明
					_self.rule = res.rule;
					setTimeout(()=>{
						_self.loading = false;
					}, 500)
					//uni.hideLoading();
				},
				fail: ()=> {
					// 加载失败提示
					api.showToast('服务器连接失败，请检查网络是否正常', 3000);
				}
			});
		},
		// 宣传记录
		shareLog() {
			_self.skipUrl('/pages/member/share_log', 2);
		},
		// 规则说明
		closeTig() {
			_self.close = !_self.close;
		},
		// 保存海报至相册
		saveShareImg() {
			// #ifdef H5
			api.showToast('请用手机截图或面对面扫码', 2000);
			// #endif
			// #ifdef APP-PLUS
			uni.showLoading({ title:'正在保存海报...',mask:true });
			var bgName = api.splitUrl(_self.bg);
			// 是否已下载
			var imgUrl = uni.getStorageSync(bgName);
			if(imgUrl){
				_self.savePoto(imgUrl);
			}else{
				// 下载背景图
				uni.downloadFile({
					url: _self.bg,
					success: (res)=>{
						//console.log(res)
						if (res.statusCode == 200){
							_self.savePoto(res.tempFilePath);
						}
						uni.hideLoading();
					}
				})
			}
			// #endif
		},
		// 保存图片至相册
		savePoto(tempFilePath) {
			uni.saveImageToPhotosAlbum({
				filePath: tempFilePath,
				success: ()=>{
					api.showToast("海报已保存至手机相册", 2000);
				},
				fail: ()=>{
					api.showToast("保存失败，请检查APP是否有权限", 5000);
				}
			});
		},
		// 跳转页面
		skipUrl(url = '/pages/login/login', type = 1) {
			if(type == 1){
				uni.reLaunch({ url: url });
			}else if(type == 2){
				uni.navigateTo({ url: url });
			}else{
				uni.switchTab({ url: url });
			}
		}
	}
}
</script>

<style>
@import url('@/components/loading/loading.css');
.explain-center{
	width: 96%;
	height: 270px;
	border-top: 1px solid #E5E5E5;
	//border-radius: 5px;
	overflow-y: scroll;
	padding: 2%;
	font-size: 13px;
	color: #666666;
	//box-shadow: 0px 4px 6px 0px rgba(103, 103, 103, 0.2);
}
.explain-title{
	width: 100%;
	height: 50px;
	line-height: 50px;
	font-size: 18px;
	text-align: left;
	color: #666666;
	font-weight: bold;
}
.close{
	width: 30px;
	height: 30px;
	float: right;
	margin: 5px -3% 0 0;
}
.explain-content{
	padding: 0 5%;
	width: 70%;
	height: 350px;
	border-radius: 10px;
	background: #FFFFFF;
	margin-top: 35%;
	margin-left: 10%;
}
.explain-tig{
	position: fixed;
	z-index: 98;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5);
}
.explain{
	position: absolute;
	width: 50px;
	height: 30px;
	line-height: 30px;
	background: #2C405A;
	right: 13px;
	color: #FFFFFF;
	text-align: center;
	border-radius: 3px;
	font-size: 15px;
}
.p-left{
	left: 30px;
}
.p-right{
	right: 30px;
}
.poster-save{ 
	width: 270upx;
	height: 90upx;
	line-height: 90upx;
	text-align: center;
	border-radius: 10px;
	position: absolute;
	bottom: 60upx;
	color: #FFFFFF;
	font-size: 35upx;
	box-shadow: 0px 4px 6px 0px rgba(103, 103, 103, 0.2);
	background-image: linear-gradient(to right, rgba(255,12,208,0.7), rgba(254,67,101,0.7));
}
.content-head{ 
	position: fixed;
	width: 30px;
	height: 30px;
	line-height: 30px;
	border-radius: 50%;
	background: rgba(0,0,0,0.5);
	top: 27px;
	left: 10px;
	text-align: center;
	font-size: 15px;
	color: #cccccc;
	z-index: 10;
	font-weight: bold; 
}
.poster{ width:100%;padding:0;margin:0;position:fixed;top:0; }
.poster-bg{ background:#FFFFFF; }
</style>
